
.calendar-input-container {
  display: inline-block;
  position: relative;
  padding: 10px;
  text-align: center;
  font-family: SimHei;
}
.calendar-input-container .fade-enter-active,
  .calendar-input-container .fade-leave-active {
    -webkit-transition: opacity .5s;
    transition: opacity .5s;
}
.calendar-input-container .fade-enter,
  .calendar-input-container .fade-leave-active {
    opacity: 0;
}
.calendar-input-container .calendar-input {
    box-sizing: border-box;
    width: 200px;
    height: 30px;
    padding-left: 5px;
    padding-right: 5px;
    border: 1px solid #ccc;
    border-radius: 4%;
    cursor: pointer;
    outline: none;
    -webkit-transition: all .6s;
    transition: all .6s;
}
.calendar-input-container #calendar {
    position: absolute;
    width: 200px;
    height: 240px;
    box-sizing: border-box;
    border: 2px solid #eee;
    border-radius: 2%;
}
.calendar-input-container #calendar div, .calendar-input-container #calendar span {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.calendar-input-container #calendar div {
      height: 12.5%;
      width: 100%;
}
.calendar-input-container #calendar span {
      height: 100%;
      width: 14.2857%;
}
.calendar-input-container #calendar #calendar-header {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.calendar-input-container #calendar #calendar-header span {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.calendar-input-container #calendar #calendar-header #date-box {
        -webkit-box-flex: 2;
            -ms-flex-positive: 2;
                flex-grow: 2;
}
.calendar-input-container #calendar .week {
      box-sizing: border-box;
      padding: 5px;
      font-size: 16px;
}
.calendar-input-container #calendar .week span {
        height: 100%;
}
.calendar-input-container #calendar .days {
      box-sizing: border-box;
      height: 75%;
      padding: 5px;
      font-size: 14px;
}
.calendar-input-container #calendar .days span {
        height: 16.666667%;
        cursor: pointer;
        -webkit-transition: all .5s;
        transition: all .5s;
}
.calendar-input-container #calendar .days .unselect {
        cursor: default;
}
.blue-theme .focus {
  border-color: #9ecaed;
  box-shadow: 0 0 5px #9ecaed;
}
.blue-theme .arrow {
  cursor: pointer;
  color: #2f80c3;
  -webkit-transition: color .5s;
  transition: color .5s;
}
.blue-theme .arrow:hover {
    color: #9ecaed;
}
.blue-theme .weekend {
  color: #2f80c3;
}
.blue-theme .days span:hover {
  border-radius: 2px;
  background-color: #9ecaed;
  color: #fff;
}
.blue-theme .days .unselect,
.blue-theme .days .unselect:hover {
  border-radius: 0;
  background-color: #fff;
  color: #ccc;
}
.blue-theme .days .select,
.blue-theme .days .select:hover {
  border-radius: 2px;
  background-color: #2f80c3;
  color: #fff;
}
.red-theme #calendar-header {
  color: #fff;
  background-color: #cb1b01;
}
.red-theme .focus {
  border-color: #ccc;
  box-shadow: 0 0 5px #ccc;
}
.red-theme .arrow {
  cursor: pointer;
}
.red-theme .weekend {
  color: #cb1b01;
}
.red-theme .days {
  color: #888;
}
.red-theme .days span:hover {
    border-radius: 2px;
    background-color: #f00;
    color: #fff;
}
.red-theme .days .unselect,
  .red-theme .days .unselect:hover {
    border-radius: 0;
    background-color: #fff;
    color: #ccc;
}
.red-theme .days .select,
  .red-theme .days .select:hover {
    border-radius: 2px;
    background-color: #cb1b01;
    color: #fff;
}

